<template>
  <div>
    <van-nav-bar
      :fixed="true"
      title="反馈列表"
      @click-left="()=>{
          $router.go(-1)
         }"
      left-arrow
      class="backgroundNaviBar"
      z-index="999"
      :border="false"
    >
      <!-- <van-icon name="add" slot="right" size="20" /> -->
    </van-nav-bar>
    <!-- <mu-appbar style="width: 100%;" class="header backgroundNaviBar">
      <a @click="backTo()">
        <mu-button icon slot="left" class="icon" size="20">
          <mu-icon value="keyboard_arrow_left"></mu-icon>
        </mu-button>
      </a>
      <span style="color:white">反馈列表</span>
    </mu-appbar> -->
    <div class="marginHeader">
      <!-- <mescroll-vue ref="mescroll" :down="mescrollDown" :up="mescrollUp" @init="mescrollInit"> -->
      <mu-container ref="container" class="demo-loadmore-content">
        <mu-load-more @refresh="refresh" :refreshing="refreshing" :loading="loading" @load="load">
          <div class="containerList" v-for="(item,index) in dataList" :key="index">
            <div class="containerListCont">
              <div class="flexheader">
                <span>反馈人：</span>
                <span>{{item.person}}</span>
              </div>
              <div class="title">
                <span>反馈主题：{{item.title}}</span>
              </div>

              <div class="flexcontainer">
                <div style="color:#ccc">
                  <textarea disabled name id="textarea" :value="item.content"></textarea>
                </div>
                <div class="name">
                  <span>时间：</span>
                  <span>{{item.gmtCreate}}</span>
                </div>
              </div>
            </div>
          </div>
        </mu-load-more>
      </mu-container>
      <!-- </mescroll-vue> -->
    </div>
  </div>
</template>
<script>
import AppBace from "../../../static/ajax.js";
import MescrollVue from "mescroll.js/mescroll.vue";
const h = document.documentElement.clientHeight || document.body.clientHeight;
const heightDevice = Math.floor((h - 56) / 155);
export default {
  data() {
    return {
      num: 5,
      refreshing: false,
      loading: false,
      token: sessionStorage.getItem("token"),
      dataList: [] // 列表数据
    };
  },
  methods: {
    refresh() {
      this.refreshing = true;
      this.$refs.container.scrollTop = 0;
      // setTimeout(() => {
        this.refreshing = false;
        // this.text = this.text === "List" ? "Menu" : "List";
        this.initData();
        this.num = 5;
      // }, 2000);
    },
    load() {
      this.loading = true;
      // setTimeout(() => {
        this.initData();
        this.num = 5;
        this.loading = false;
        this.num += 5;
      // }, 2000);
    },
    mescrollInit(mescroll) {
      this.mescroll = mescroll; // 如果this.mescroll对象没有使用到,则mescrollInit可以不用配置
    },
    backTo() {
      this.$router.go(-1);
    },
    async initData() {
      var url = "/app/feedBack";
      var pageNum = 1;
      // page.num
      var pageSize = this.num;
      // page.size
      var res = await AppBace.request(
        url,
        "GET",
        {
          pageNum,
          pageSize
        },
        this.token
      );
      console.log(res);
      this.dataList = res.data.data.rows;
    }
  },
  beforeRouteEnter(to, from, next) {
    // 如果没有配置回到顶部按钮或isBounce,则beforeRouteEnter不用写
    next(vm => {
      // 找到当前mescroll的ref,调用子组件mescroll-vue的beforeRouteEnter方法
      vm.$refs.mescroll && vm.$refs.mescroll.beforeRouteEnter(); // 进入路由时,滚动到原来的列表位置,恢复回到顶部按钮和isBounce的配置
    });
  },
  beforeRouteLeave(to, from, next) {
    // 如果没有配置回到顶部按钮或isBounce,则beforeRouteLeave不用写
    // 找到当前mescroll的ref,调用子组件mescroll-vue的beforeRouteLeave方法
    this.$refs.mescroll && this.$refs.mescroll.beforeRouteLeave(); // 退出路由时,记录列表滚动的位置,隐藏回到顶部按钮和isBounce的配置
    next();
  },
  created() {
    this.initData();
  },
  async mounted() {
    
  },
  components: {
    MescrollVue
  }
};
</script>
<style scoped>
.flexcontainer {
  display: flex;
  flex-direction: column;
  /* justify-content: space-between; */
  /* height: 40px; */
  /* padding: 5px; */
  flex-wrap: wrap;
  /* align-items: center; */
}
.containerList {
  background: white;
  padding: 10px;
  border-bottom: 1px #ccc solid;
  margin-bottom: 10px;
  border-radius: 5px;
}
.containerListCont {
  /* height: 80px; */
  display: flex;
  flex-direction: column;
  justify-content: center;
}
.header {
  position: fixed;
  top: 0;
  width: 100%;
}
.marginHeader {
  margin-top: 46px;
  /* padding: 10px; */
  padding-top:10px; 
}
.title {
  font-size: 14px;
  font-weight: 600;
  color: #333333;
  height: 30px;
  display: flex;
  align-items: center;
  /* justify-content: center; */
}
.timeStyle {
  color: rgb(102, 101, 101);
}
.name {
  text-align: right;
  color: #c6c6c6;
  font-size: 11px;
}
.flexheader {
  border-bottom: 1px rgb(223, 222, 222) solid;
  font-size: 12px;
  color: #999999;
  height: 30px;
  display: flex;
  align-items: center;
}
#textarea {
  width: 100%;
  border: none;
  background-color: white;
  font-size: 12px;
  color: #999999;
  /* height: 200px; */
}
</style>


